button {
  border-radius: 7px;
  height: 50px;
  font-size: 20px;
  background-color: #83e377;
  color: #DA667B;
  font-weight: bold;
}

button:hover {
  background-color: white;
  color: #DA667B;
  cursor: pointer;
}

.noMatchContainer > a:link {
  text-decoration: none;
  margin: auto;
  height: 50px;
}
